import React, { Fragment, useState, useEffect } from 'react';
import './Orders.scss';
import CustomInput from '../../CustomInput';
import fakeCar from '../../../resources/car.png';
import { useHistory } from 'react-router-dom';
import AdminLoader from '../AdminLoader';
import AdminPagination from '../AdminPagination';
import AdminRequest from '../AdminRequest';
export default function Orders({ api, headers, getCookie }) {
  const options = [
    { name: 'isFullTank', description: ' ' },
    { name: 'isNeedChildChair', description: ' ' },
    { name: 'isRightWheel', description: ' ' },
  ];
  const history = useHistory();
  const [viewFilter, setViewFilter] = useState(false);
  const [isLoad, setLoad] = useState(true);
  const [page, setPage] = useState(1);
  const [currentOrder, setCurrentOrder] = useState({});
  const [countPages, setCountPages] = useState(1);
  const [filterList, setFilterList] = useState({
    cities: [],
    cars: [],
    statuses: [],
  });

  const [filter, setFilter] = useState({
    date: 0,
    carId: '',
    cityId: '',
    status: '',
  });

  useEffect(() => {
    getOrderTable();
  }, [page]);

  function getOrderTable() {
    setLoad(true);
    const { carId, date, cityId, status } = filter;
    const getTable = new AdminRequest(
      `db/order?page=${page - 1}&limit=1&createdAt[$gt]=${date}${
        carId && '&carId=' + carId
      }${cityId && '&cityId=' + cityId}${
        status && '&orderStatusId=' + status
      }`,
      'GET',
      `Bearer ${getCookie('access_token')}`
    );
    getTable
      .doRequest()
      .then((res) => {
        setCurrentOrder(res.data[0]);
        setCountPages(res.count);
        setLoad(false);
      })
      .catch((err) => {
        history.push('/admin/error-page');
        console.error('ERROR', err);
      });
  }


  function filterHandler(event) {
    const now = new Date();
    const { name, value } = event.target;
    if (name === 'date') {
      switch (value) {
        case 'all':
          setFilter({ ...filter, [name]: 0 });
          break;
        case 'month':
          setFilter({
            ...filter,
            [name]: now.setMonth(now.getMonth() - 1),
          });
          break;
        case 'week':
          setFilter({ ...filter, [name]: now.setDate(now.getDate() - 7) });
          break;
        case 'day':
          setFilter({ ...filter, [name]: now.setDate(now.getDate() - 1) });
          break;
        case 'hours':
          setFilter({
            ...filter,
            [name]: now.setHours(now.getHours() - 3),
          });
          break;
      }
    } else if (
      name === 'carId' ||
      name === 'cityId' ||
      name === 'status'
    ) {
      setFilter({ ...filter, [name]: value });
    }
  }

  function createFilters() {
    const getCars = new AdminRequest('db/car/', 'GET');
    const getCities = new AdminRequest('db/city/', 'GET');
    const getOrderStatus = new AdminRequest('db/orderStatus/', 'GET');

    if (!viewFilter) {
      let cars;
      let cities;
      let statuses;
      getCities
        .doRequest()
        .then(({ data }) => {
          cities = data;
        })
        .then(() => {
          getCars
            .doRequest()
            .then(({ data }) => {
              cars = data;
            })
            .then(() => {
              getOrderStatus
                .doRequest()
                .then(({ data }) => {
                  statuses = data;
                })
                .then(() =>
                  setFilterList({
                    cars: cars,
                    cities: cities,
                    statuses: statuses,
                  })
                );
            });
        });
      setViewFilter(!viewFilter);
    } else {
      setViewFilter(!viewFilter);
    }
  }

  return (
    <>
      <h1 className='admin__heading'></h1>
      <div className='order-block'>
        <div className='order-block__sort-container'>
          <span onClick={createFilters} className='filter-icon'></span>

          {viewFilter && (
            <form className='order-block__sort-container__sort'>
              <select
                onChange={filterHandler}
                name='date'
                className='admin__select'
              >
                <option value='all'>  </option>
                <option value='month'> </option>
                <option value='week'> </option>
                <option value='day'> </option>
                <option value='hours'>  </option>
              </select>
              <select
                onChange={filterHandler}
                className='admin__select'
                name='carId'
              >
                <option value=''> </option>
                {filterList &&
                  filterList.cars.map((el) => (
                    <option key={el.id} value={el.id}>
                      {el.name}
                    </option>
                  ))}
              </select>
              <select
                onChange={filterHandler}
                className='admin__select'
                name='cityId'
              >
                <option value=''> </option>
                {filterList &&
                  filterList.cities.map((el) => (
                    <option key={el.id} value={el.id}>
                      {el.name}
                    </option>
                  ))}
              </select>
              <select
                onChange={filterHandler}
                className='admin__select'
                name='status'
              >
                <option value=''> </option>
                {filterList &&
                  filterList.statuses.map((el) => (
                    <option key={el.id} value={el.id}>
                      {el.name}
                    </option>
                  ))}
              </select>
              <button
                onClick={getOrderTable}
                className='admin__button blue'
                type='button'
              >
                
              </button>
            </form>
          )}
        </div>

        <div className='order-block__info'>
          {isLoad ? (
            <AdminLoader />
          ) : currentOrder ? (
            <>
              <span className='order-block__info__part'>
                <img
                  crossOrigin='anonymous'
                  referrerPolicy='origin'
                  className='order-block__info__img'
                  src={
                    currentOrder.carId.thumbnail.path
                      ? `http://api-factory.simbirsoft1.com${currentOrder.carId.thumbnail.path}`
                      : fakeCar
                  }
                  alt='car'
                />
                <p className='order-block__info__text'>
                  <span>{currentOrder.carId.name || ''}</span> {' '}
                  <span>
                    {currentOrder.cityId.name || ''}{' '}
                    {currentOrder.pointId.address || ''}
                  </span>{' '}
                  <br />
                  {new Date(currentOrder.dateFrom).toLocaleString('RU') ||
                    '01.01.2001 01:01'}{' '}
                  -{' '}
                  {new Date(currentOrder.dateTo).toLocaleString('RU') ||
                    '01.01.2001 01:01'}
                  <br />
                  : <span>{currentOrder.color || ''}</span>
                </p>
              </span>
              <span className='order-block__info__part'>
                <span className='order-block__info__chechboxes'>
                  {options.map((el, i) => (
                    <Fragment key={i}>
                      <CustomInput
                        type='checkbox'
                        description={el.description}
                        name={el.name}
                        checked={currentOrder[el.name]}
                        readOnly={true}
                      />
                      <br />
                    </Fragment>
                  ))}
                </span>
                <p className='order-block__info__cost'>
                  {currentOrder.price || '00000'} 
                </p>
                <span className='order-block__info__buttons'>
                  <button className='order-block__info__buttons__ok'>
                    <span></span> 
                  </button>
                  <button className='order-block__info__buttons__cancel'>
                    <span></span> 
                  </button>
                  <button className='order-block__info__buttons__edit'>
                    <span></span> 
                  </button>
                </span>
              </span>
            </>
          ) : (
            '  '
          )}
        </div>
        {currentOrder && (
          <AdminPagination
            setPage={setPage}
            page={page}
            countPages={countPages}
          />
        )}
      </div>
    </>
  );
}
